import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ShareIcon from '@material-ui/icons/Share';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import LinkTwoToneIcon from '@material-ui/icons/LinkTwoTone';
import EditTwoToneIcon from '@material-ui/icons/EditTwoTone';
import Button from '@material-ui/core/Button';
import { shortenText } from '../libs/helpers'

const useStyles = makeStyles((theme) => ({
  root: {
		maxWidth: 240,
		borderRadius: '5%'
  },
  media: {
    height: 0,
    paddingTop: '56.25%', // 16:9
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  avatar: {
    backgroundColor: red[500],
	},
	title: {
		float: 'left',
		display: 'inline'
	},
	price: {
		float: 'right',
		display: 'inline'
	},
	cardSection: {
		display: 'block',
		marginBottom: theme.spacing(3),
		fontWeight: 'bold'
	},
	image: {
		width: '100%',
		height: 'auto'
	},
	action: {
		position: 'absolute',
		top: '0%',
		left: '0%',
		transform: 'translate(250%, 0%)',
	},
	imageContainer: {
		position: 'relative'
	}
}));

export default function PreviewCard({ productLink, showStaging, stageProduct, ...rest }) {

	const classes = useStyles();

  return (
    <Card className={classes.root}>
			<div className={classes.imageContainer}>
				<img className={classes.image} width="240" height="240" src={productLink.image} />

				<CardHeader
					className={classes.action}
					action={
						<IconButton aria-label="settings">
							<EditTwoToneIcon />
						</IconButton>
					}
				/>
			</div>

     	<CardContent>
				{productLink !== undefined && (<div className={classes.cardSection}>
					<Typography variant='inherit' className={classes.title}>
						{shortenText(productLink.product, 24)}
					</Typography>
					{productLink.price !== undefined && (<Typography variant='inherit' className={classes.price}>
						{productLink.price}
					</Typography>)}
				</div>)}
        {productLink.description !== undefined && (<Typography variant="body1" color="textSecondary" component="p">
					{shortenText(productLink.description, 100)}
        </Typography>)}
				{productLink.url !== undefined && (<Typography variant="body2" component="p" noWrap>
					<LinkTwoToneIcon/> <a href={productLink.url}>{productLink.url}</a>
				</Typography>)}
      </CardContent>

			{showStaging && 
				(<CardActions disableSpacing>
					<Button size="small" color="primary" onClick={() => stageProduct(productLink)}>
						Stage
					</Button>
				</CardActions>)}
    </Card>
  );
}
