import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import axios from 'axios';
import { makeStyles } from '@material-ui/core/styles';
import Snackbar from '@material-ui/core/Snackbar';
import TextField from '@material-ui/core/TextField';
import MuiAlert from '@material-ui/lab/Alert';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import { FormControl } from '@material-ui/core';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Select from '@material-ui/core/Select';
import { CssBaseline, Avatar, Typography, Button, Container } from "@material-ui/core";
import CircularProgress from '@material-ui/core/CircularProgress';
import { green } from '@material-ui/core/colors';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import { validURL } from '../libs/helpers'

function Alert(props) {
	return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const useStyles = makeStyles((theme) => ({
	root: {
		'& > *': {
			margin: theme.spacing(1),
			width: '25ch',
		},
	},
	wrapper: {
		margin: theme.spacing(1),
		position: 'relative',
	},
	margin: {
		margin: theme.spacing(1),
	},
	paper: {
		marginTop: theme.spacing(2),
		display: 'flex',
		flexDirection: 'column',
		alignItems: 'center',
	},
	avatar: {
		margin: theme.spacing(1),
		backgroundColor: theme.palette.secondary.main,
	},
	form: {
		width: '100%', // Fix IE 11 issue.
		marginTop: theme.spacing(1),
	},
	formControl: {
		marginTop: theme.spacing(1),
		width: '100%',
		minWidth: 120,
	},
	submit: {
		margin: theme.spacing(3, 0, 2),
	},
	buttonProgress: {
		color: green[500],
		position: 'absolute',
		top: '50%',
		left: '50%',
		marginTop: -12,
		marginLeft: -12,
	},
}));

export default function PreviewLinkForm() {

	const classes = useStyles();
  const [openError, setOpenError] = useState(false);
	const [errorMessage, setErrorMessage] = useState("");
	const [link, setLink] = useState("")
	const [product, setProduct] = useState("")
	const [description, setDescription] = useState("")
	const [price, setPrice] = useState("")
	const [image, setImage] = useState(null)
	const [isLoading, setIsLoading] = useState(false)
	
	function handleSubmit() {

	}

	function validateForm() {
		return validURL(link) && (product !== "")
	}

	const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setOpenError(false);
  };

	return (
		<React.Fragment>
			<CssBaseline/>
			<Container maxWidth='xs'>
				<div className={classes.paper}>
					<Avatar className={classes.avatar}>
						<AddCircleIcon />
					</Avatar>
					<Typography component="h1" variant="h5">
						New Product Link
					</Typography>
					<form onSubmit={handleSubmit} className={classes.form}>
						<TextField
							variant='outlined'
							type="text"
							label="Product Link"
							margin="normal"
							fullWidth={true}
							onChange={e => setLink(e.target.value)}
							required
						/>
						<TextField
							variant='outlined'
							type="text"
							label="Product Name"
							margin="normal"
							fullWidth={true}
							onChange={e => setProduct(e.target.value)}
							required
						/>
						<TextField
							variant='outlined'
							type="text"
							label="Description"
							margin="normal"
							multiline
							rows={3}
							fullWidth={true}
							onChange={e => setDescription(e.target.value)}
						/>
						<TextField
							variant='outlined'
							type="text"
							label="Price"
							margin="normal"
							fullWidth={true}
							onChange={e => setPrice(e.target.value)}
						/>
						<FormControl variant="outlined" className={classes.formControl}>
							<InputLabel>Product Image (Max Size: 500 KB, .png/.jpg)</InputLabel>
							<input
								id="single"
								type="file"
								fullWidth={true}
								accept="image/png, image/jpeg"
								onChange={(e) => setImage(e.target.files[0])}
							/>
						</FormControl>
						<div className={classes.wrapper}>
							<Button
								variant='contained'
								type='submit'
								color='secondary'
								fullWidth={true}
								disabled={!validateForm()}
								className={classes.submit}>
								{'Add'}
							</Button>
							{isLoading && <CircularProgress size={24} className={classes.buttonProgress} />}
						</div>
					</form>
					<Snackbar open={openError} autoHideDuration={6000} onClose={handleClose}>
						<Alert onClose={handleClose} severity="error">
						{errorMessage}
						</Alert>
					</Snackbar>
				</div>
			</Container>
		</React.Fragment>
	);
}