import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ShareIcon from '@material-ui/icons/Share';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import LinkTwoToneIcon from '@material-ui/icons/LinkTwoTone';
import EditTwoToneIcon from '@material-ui/icons/EditTwoTone';
import Button from '@material-ui/core/Button';
import { shortenText } from '../libs/helpers'

const useStyles = makeStyles((theme) => ({
  root: {
		display: 'flex',
		maxWidth: 300,
		borderRadius: '5%'
  },
  media: {
    height: 0,
    paddingTop: '56.25%', // 16:9
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  avatar: {
    backgroundColor: red[500],
	},
	title: {
		float: 'left',
		display: 'inline'
	},
	price: {
		float: 'right',
		display: 'inline'
	},
	cardSection: {
		display: 'block',
		marginBottom: theme.spacing(3),
		fontWeight: 'bold'
	},
	image: {
		width: '100%',
		height: 'auto'
	},
	action: {
		position: 'absolute',
		top: '0%',
		left: '0%',
		transform: 'translate(250%, 0%)',
	},
	imageContainer: {
		position: 'relative'
	},
	display: 'flex',
	details: {
    display: 'flex',
    flexDirection: 'column',
  },
  content: {
    flex: '1 0 auto',
  },
  cover: {
		width: '40%'
  },
  controls: {
    display: 'flex',
    alignItems: 'center',
    paddingLeft: theme.spacing(1),
    paddingBottom: theme.spacing(1),
  },
}));

export default function StageCard({ productLink, showStaging, stageProduct, ...rest }) {

	const classes = useStyles();

  return (
		<Card className={classes.root}>
      <div className={classes.details}>
        <CardContent className={classes.content}>
					{productLink !== undefined && (
						<Typography component="h5" variant="h5">
							{productLink.product}
						</Typography>
					)}
					{productLink.url !== undefined && (<Typography variant="body2" component="p" noWrap>
					<LinkTwoToneIcon/> <a href={productLink.url}>{shortenText(productLink.url, 20)}</a>
				</Typography>)}
        </CardContent>

        <div className={classes.controls}>
					<Button size="small" color="primary" onClick={() => stageProduct(productLink)}>
						Remove
					</Button>
					<Button size="small" color="primary" onClick={() => stageProduct(productLink)}>
						Show
					</Button>
        </div>
      </div>

      <img
        className={classes.cover}
        src={productLink.image}
      />
    </Card>
  );
}
