import React, {useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import AddCircleTwoToneIcon from '@material-ui/icons/AddCircleTwoTone';
import IconButton from '@material-ui/core/IconButton';
import Modal from '@material-ui/core/Modal';
import PreviewCard from '../components/PreviewCard'
import PreviewLinkForm from '../components/PreviewLinkForm'
import StageCard from '../components/StageCard'
import TextField from '@material-ui/core/TextField';
import { Typography, Divider, GridList, GridListTile, Button, FormControl, FormGroup, FormLabel } from '@material-ui/core'
import { validURL } from '../libs/helpers'
import { testLinks } from '../libs/testData'
import _ from 'lodash'

function rand() {
  return Math.round(Math.random() * 20) - 10;
}

function getModalStyle() {
  const top = 50
  const left = 50

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

const useStyles = makeStyles((theme) => ({
	addPaper: {
		width: '240px',
		height: '100%',
		borderRadius: '5%',
		display: 'flex',
    flexDirection: 'column',
		alignItems: 'center',
		paddingTop: theme.spacing(22),
	},
	addIcon: {
		width: 60,
		height: 60
	},
	paper: {
    position: 'absolute',
    width: 400,
    backgroundColor: theme.palette.background.paper,
		borderRadius: '5%',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(1, 1, 1),
	},
	gridList: {
    flexWrap: 'nowrap',
    // Promote the list into his own layer on Chrome. This cost memory but helps keeping high FPS.
    transform: 'translateZ(0)',
	},
	stagePlaceholder: {
		padding: theme.spacing(5),
    display: 'flex',
    flexDirection: 'column',
		alignItems: 'center'
	},
	liveFormInput: {
		labelPlacement:"start",
		marginRight: theme.spacing(1),
	},
	liveForm: {
		display:'flex',
		flexDirection:'row',
		justifyContent:'center'
	},
	stageMessageContainer: {
		display:'flex',
		flexDirection:'row',
		marginBottom: theme.spacing(1)
	},
	stageMessage: {
		marginRight: theme.spacing(1),
	},
	productsContainer: {
		marginTop: theme.spacing(2)
	}
}));

function LiveStage(props) {
	const classes = useStyles()
	const [modalStyle] = React.useState(getModalStyle);
	const [open, setOpen] = useState(false)
	const [liveLink, setLiveLink] = useState('')
	const [showStaging, setShowStaging] = useState(false)
	const [stagedLinks, setStagedLinks] = useState([])

	const body = (
    <div style={modalStyle} className={classes.paper}>
      <PreviewLinkForm />
    </div>
	);

	function startStaging(e) {
		e.preventDefault()
		if (validURL(liveLink)) {
			setShowStaging(true)
		}
	}

	function endStaging(e) {
		e.preventDefault()
		setShowStaging(false)
		setLiveLink("")
	}

	function stageProduct(product) {
		setStagedLinks([...stagedLinks, product])
	} 
	
	return (
		<React.Fragment>
			<div className={classes.productsContainer}>
				{!showStaging && (
					<React.Fragment>
						<Typography variant="h6" gutterBottom>
							What's the Youtube URL you want the product links to be displayed on?
						</Typography>
						<FormControl>
							<FormGroup className={classes.liveForm}>
								<TextField
									variant='outlined'
									type="text"
									label="Youtube Livestream URL"
									place="www.youtube.com/jdnadkjnfj"
									margin="dense"
									onChange={e => setLiveLink(e.target.value)}
									required
									autoFocus
									// size="small"
									color="primary"
									className={classes.liveFormInput}
								/>
								<Button variant="text" color="primary" onClick={startStaging} size="small">Go Live</Button>
							</FormGroup>
						</FormControl>
					</React.Fragment>
				)}
			</div>
			
			<div className={classes.productsContainer}>
				{showStaging && (
					<React.Fragment>
						<Typography gutterBottom variant="h4">
							Staged Product Links
						</Typography>
						<div className={classes.stageMessageContainer}> 
							<Typography variant="body1" className={classes.stageMessage}>
								Your products will be displayed on your livestream at &nbsp;
								<a href={liveLink}>{liveLink}</a>
							</Typography>
							<Button variant="text" color="secondary" size="small" onClick={endStaging}><u>End Live</u></Button>
						</div>
					</React.Fragment>
				)}

				{(showStaging && stagedLinks.length == 0) && (
					<Paper elevation={0}>
						<div className={classes.stagePlaceholder}>
							<Typography variant="body1">
								Select the product links that you want to show on your livestream and stage them here.
							</Typography>
						</div>
					</Paper>
				)}
				
				{(showStaging && stagedLinks.length > 0) && (
					<Grid container spacing={2} cols={5}>
						{_.map(stagedLinks, (stagedLink) => {
							return (<Grid item xs={12} sm="auto" key={stagedLink.id}>
												<StageCard productLink={stagedLink} />
											</Grid>)
						})}
					</Grid>
				)}
			</div>
			
			<div className={classes.productsContainer}>
				<Typography gutterBottom variant="h4">
					All Product Links
				</Typography>
				<Grid container spacing={2}>
					<Grid item xs={12} sm="auto">
						<Paper elevation={0} className={classes.addPaper}>
							<IconButton iconStyle={classes.addIcon} onClick={()=>setOpen(true)}>
								<AddCircleTwoToneIcon fontSize='large' />
							</IconButton>
						</Paper>
					</Grid>
					{_.map(testLinks, (testLink) => {
						if (testLink.product !== undefined && testLink.url !== undefined) {
							return (<Grid item xs={12} sm="auto" key={testLink.id}>
												<PreviewCard showStaging={showStaging} productLink={testLink} stageProduct={stageProduct} />
											</Grid>)
						}
					})}
				</Grid>
			</div>

			<Modal
        open={open}
        onClose={() => setOpen(false)}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
      >
        {body}
      </Modal>

		</React.Fragment>
	)
}

export default LiveStage